* {
  color: white;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: tilt neon;
}

.App {
  text-align: center;
}

body {
  background-color: rgb(0, 0, 0);
}
.ppic{
  border-radius: 100px;
}
.AuthScreen {
  display: flex;
  justify-content: center;
  align-items: center;

}

.auth-box {
  margin-top: 3cm;
  width: 16cm;
  // border: 1px solid white;
  border-radius: 10px;
  background-color: #0f0f13;
  padding: .3cm .6cm;
}

input:focus {
  outline: none;
  border: 2.5px solid royalblue;
}

.welcome {
  margin-bottom: -.45cm;
}

#usernameExist {
  display: none;
}

#whitespaceFound {
  display: none;
}

#twoDots {
  display: none;
}

#otherThanUnderscore {
  display: none;
}

#capitalUsername {
  display: none;
}

.error {
  font-size: 15px;
  color: rgb(252, 47, 47);
}

.tagline {
  font-size: 62px;
  line-height: 1.75cm;
  // font-weight: 600;
  color: rgb(243, 243, 243);
  font-family: Tilt Neon;
}

.sub-tagline {
  font-size: 20px;
  margin-top: .3cm;
  color: rgb(96, 96, 96);
  font-family: Tilt Neon;
}

.google-button {
  margin-top: 1.3cm;
  margin-bottom: .5cm;
  background-color: transparent;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family: Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  transition: .25s;

  &:hover {
    background-color: #2d2d2d;

  }
}

.Nav {
  padding: .3cm 10vw;
  background-color: #0f0f13;
}

.name {
  font-family: Tilt Neon;
  font-size: 30px;
  cursor: pointer;
}

.setPassword {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 10;
  display: flex;
  justify-content: center;

  backdrop-filter: blur(10px);


}

.saveU {
  width: 100%;
  background-color: transparent;
  color: white;
  border: 3px solid #2d2d2d;
  padding: .3cm .7cm;
  font-family: Tilt Neon;
  border-radius: 10px;
  font-size: 18px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  transition: .25s;

  &:hover {
    background-color: #2d2d2d;

  }

}

#setUsername {
  display: none;
}


.user-form {
  display: flex;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.user-input {
  background-color: transparent;
  color: white;
  font-family: Tilt Neon;
  border: 3px solid #2d2d2d;
  font-size: 18px;
  padding: 0.3cm 0.3cm;
  /* Adjusted padding values */
  width: calc(100% - 0.8cm);
  /* Adjusted width to account for padding */
  border-radius: 10px;
}

.log-out-box {
  font-family: tilt neon;
  font-size: 16px;
  margin-top: .3cm;
  cursor: pointer;
  color: rgb(97, 97, 97);
  transition: .2s;

  &:hover {
    color: white;
  }
}

.username-box-head {
  width: 100%;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.username-box {
  display: flex;
  width: 10cm;
  margin-top: 3cm;
  height: fit-content;
  row-gap: .3cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #0f0f13;
  padding: .0cm .5cm .5cm .5cm;
  border-radius: 10px;
}

.menu-item {
  cursor: pointer;
  font-family: Tilt neon;
  font-size: 16px;
}

.menu {
  display: flex;
  column-gap: .5cm;
  align-items: center;
}

.nav-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}